import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { ko } from 'date-fns/locale';

const Register_form = () => {
    //    (role)
    const { value } = useParams(); //creator / user
    const roleText = value === 'creator' ? '' : '';
    const role = value === 'creator' ? 'ROLE_CREATOR' : 'ROLE_USER';

    // role  
    useEffect(() => {
        setFormValues(prevState => ({
            ...prevState,
            userRole: role
        }));
    }, [value]);

    // form 
    const [formValues, setFormValues] = useState({
        userId: '',
        userPwd: '',
        userPhone: '',
        userNickname: '',
        userEmail: '',
        userRole: '',
        marketingAgreement: '',
        userBirth: ''
    });

    // /   ( )
    const [isChecked_a, setIsChecked_a] = useState(false); // 
    const [isChecked_b, setIsChecked_b] = useState(false); // 
    
    //  
    const handleCheckbox_a_Change = () => {
        setIsChecked_a(!isChecked_a);
    };

    //  
    const handleCheckbox_b_Change = () => {
        setIsChecked_b(!isChecked_b); //   
        setFormValues(prevState => ({
            ...prevState,
            marketingAgreement: isChecked_b ? 'n' : 'y' // isChecked_b true  '', false  'y'
        }));
    };

    //   
    const handleBirthChange = (date) => {
        // date null  ,  YYYY-MM-DD   
        setFormValues(prevState => ({
            ...prevState,
            userBirth: date ? `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}` : ''
        }));
    };
    
    // input   
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormValues(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    //  
    const [userPwdCheck, setUserPwdCheck] = useState('');
    //    
    const handlePwdCheckChange = (e) => {
        const { value } = e.target;
        setUserPwdCheck(value);
    };

    //    (1  )
    const [isUserIdDuplicate, setIsUserIdDuplicate] = useState(null);

    //    
    const idCheck = async () => {
        try {
            const response = await axios.get('/user/userIdCheck', {
                params: {
                    userId: formValues.userId
                }
            });
            setIsUserIdDuplicate(response.data); //     
        } catch (error) {
            console.error('  :', error);
            //    
        }
    };

    //  
    const registerSubmit = async (e) => {
        e.preventDefault();

        //    
        if (!isChecked_a) {
            alert("    .");
            return;
        }
        //    or  
        if (isUserIdDuplicate !== 1 || isUserIdDuplicate === null) {
            alert("  !");
            return;
        }
        //      
        if (formValues.userPwd !== userPwdCheck || formValues.userPwd === '') {
            alert("  !");
            return;
        }

        try {
            const response = await axios.post('/user/register', formValues, {
                withCredentials: true
            });
            if (response.data === "success") {
                window.location.href = `/Register_ok/${value}`;
            } else {
                alert(" !");
            }
        } catch (error) {
            alert("!");
            console.error(' :', error.response.data);
        }
    };
    
    return (
        <div className='content_body'>
            <div className='registerContainer'>
                <h2>{roleText}&nbsp;&nbsp;</h2>
                
                <section className='register_phase'>
                    <div className='purpleCircle active'>1</div>
                    
                    <div className='grayline'></div>

                    <div className='purpleCircle'>2</div>
                    <div className='phaseText'>
                        <p>  </p>
                        <p></p>
                    </div>
                </section>

                <form onSubmit={registerSubmit}>
                    <section className='register_form'>
                        <p><purple>*</purple> </p>

                    
                        <div className='inputs_id'>
                            <div className='cols'><purple>*</purple>  </div>
                            <div><input type='text' name='userId' value={formValues.userId} onChange={handleChange}  placeholder=' '></input></div>
                            <div><button id='idCheckBtn' type='button' onClick={idCheck}></button></div>
                            {isUserIdDuplicate === 0 && <div style={{ color: 'red' }}>   .</div>}
                            {isUserIdDuplicate === 1 && <div style={{ color: 'blue' }}>  .</div>}
                            {isUserIdDuplicate === 2 && <div style={{ color: 'red' }}> !</div>}
                        </div>
                        <div className='inputs'>
                            <div className='cols'><purple>*</purple>  </div>
                            <div><input type='password' name='userPwd' value={formValues.userPwd} onChange={handleChange} placeholder=' '></input></div>
                        </div>
                        <div className='inputs'>
                            <div className='cols'><purple>*</purple>   </div>
                            <div><input type='password' name='userPwdCheck' value={userPwdCheck} onChange={handlePwdCheckChange} placeholder=' '></input></div>
                            {userPwdCheck === formValues.userPwd && formValues.userPwd !== '' && <div style={{ color: 'blue' }}> .</div>}
                            {userPwdCheck !== formValues.userPwd && <div style={{ color: 'red' }}>  .</div>}
                        </div>

                        <div className='inputs'>
                            <div className='cols'><purple>*</purple>  </div>
                            <div className='birthdate'>
                                <DatePicker
                                    locale={ko}
                                    selected={formValues.userBirth ? new Date(formValues.userBirth) : null}
                                    onChange={handleBirthChange}
                                    dateFormat="yyyy-MM-dd"
                                    placeholderText="   . (ex> 1994-10-25)"
                                />
                            </div>
                        </div>

                        <div className='inputs'>
                            <div className='cols'><purple>*</purple>  </div>
                            <div><input type='text' name='userPhone' value={formValues.userPhone} onChange={handleChange}  placeholder="'-'  "></input></div>
                        </div>
                        <div className='inputs'>
                            <div className='cols'><purple>*</purple>  </div>
                            <div><input type='text' name='userNickname' value={formValues.userNickname} onChange={handleChange} placeholder=' '></input></div>
                        </div>
                        <div className='inputs'>
                            <div className='cols'><purple>*</purple>  </div>
                            <div><input type='text' name='userEmail' value={formValues.userEmail} onChange={handleChange} placeholder=' '></input></div>
                        </div>
                    </section>

                    <div className='checkAgree'>
                        <label>
                            <input
                                type='checkbox'
                                checked={isChecked_a}
                                onChange={handleCheckbox_a_Change}
                            />
                            <span><red>[]</red>  </span>
                        </label>
                        <label>
                            <input
                                type='checkbox'
                                checked={isChecked_b}
                                onChange={handleCheckbox_b_Change}
                            />
                            <span>[]    </span>
                        </label>
                        <br/>
                        <button id='backBtn' type='button' onClick={() => { window.location.href = '/register' }}></button>
                        <button id='registerBtn' type="submit"></button>
                    </div>


                </form>

            </div>
        </div>
    );
};

export default Register_form;