import React, { createContext, useState, useEffect, useContext  } from 'react';
import Cookies from 'js-cookie';

export const AuthContext = createContext();

const AuthProvider = ({ children }) => {
    //     
    const tokenCookie = Cookies.get('token'); //
    const isLoginFromCookie = Cookies.get('isLogin') === 'true'; //
    const userNicknameCookie = Cookies.get('userNickname'); //
    const userRoleCookie = Cookies.get('userRole'); //
    const userIdCookie = Cookies.get('userId'); //

    // 
    const login = (newToken, newUserId, newUserNickname, newUserRole) => {
      Cookies.set('token', newToken, { expires: 7 }); //   , 7 
      Cookies.set('userNickname', newUserNickname, { expires: 7 }); //    
      Cookies.set('userRole', newUserRole, { expires: 7 }); //    
      Cookies.set('userId', newUserId, { expires: 7 }); //    
      Cookies.set('isLogin', true, { expires: 7 }); //  true  

      
    };

    // 
    const logout = () => {
      if (window.confirm(" ?")) {
          Cookies.remove('token'); //   
          Cookies.remove('isLogin'); //    
          Cookies.remove('userNickname'); //    
          Cookies.remove('userRole'); //    
          Cookies.remove('userId'); //    
          window.location.reload(); //  
      }
  };

    useEffect(() => {
      //      
      const token = Cookies.get('token');
      const isLogin = Cookies.get('isLogin') === 'true';
      const userNickname = Cookies.get('userNickname');
      const userRole = Cookies.get('userRole');
      const userId = Cookies.get('userId');

      //      
      if (token && isLogin && userNickname && userRole && userId) {
        login(token, userId, userNickname, userRole);
      }
    }, []);

    return (
      <AuthContext.Provider
        value={{
          tokenCookie,
          isLoginFromCookie,
          userNicknameCookie,
          userRoleCookie,
          userIdCookie,
          login,
          logout
        }}
      >
        {children}
      </AuthContext.Provider>
    );
  };
  
  export default AuthProvider;