import './App.css';
import './css/user.css';
import './css/login.css';
import './css/myinfo.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'; //
import AuthProvider from './pages/provider/AuthProvider' //, 

import Header from './pages/fragment/Header'; // 
import Footer from './pages/fragment/Footer'; // 
import Main from './pages/main'; // 

import Notice from './pages/notice/notice'; // 
import Creator from './pages/creator/creator'; //  
import Register from './pages/user/Register'; //  ( )
import Register_form from './pages/user/Register_form'; //  ()
import Register_ok from './pages/user/Register_ok'; //  
import Login from './pages/user/Login'; // 
import Myinfo from './pages/user/Myinfo/Myinfo'; // 

import UserInfo from './pages/user/Myinfo/Userinfo'; // > 
import MyProfile from './pages/user/Myinfo/MyProfile'; // >  
import MyQuestions from './pages/user/Myinfo/MyQuestions'; // >  
import MyPosts from './pages/user/Myinfo/MyPosts'; // >  
import Withdraw from './pages/user/Myinfo/Withdraw'; // > 

const App = () => {
  return (
    <AuthProvider>
      <div>
        <Header /> {/*  */}

        {/*  */}
        <Router>
          <Routes>
            <Route path="/" element={<Main />} />
            <Route path="/notice" element={<Notice />} />
            <Route path="/creator" element={<Creator />} />
            <Route path="/register" element={<Register />} />
            <Route path="/Register_form/:value" element={<Register_form />} />
            <Route path="/Register_ok/:value" element={<Register_ok />} />
            <Route path="/login" element={<Login />} />
            
            <Route path="/myinfo/*" element={<Myinfo />}>
              <Route index path="" element={<UserInfo />} />
              <Route path="UserInfo" element={<UserInfo />}></Route>
              <Route path="MyProfile" element={<MyProfile />}></Route>
              <Route path="MyQuestions" element={<MyQuestions />}></Route>
              <Route path="MyPosts" element={<MyPosts />}></Route>
              <Route path="Withdraw" element={<Withdraw />}></Route>
            </Route>

          </Routes>
        </Router>

        <Footer /> {/*  */}
      </div>
    </AuthProvider>
  );
};

export default App;