import React, {useState,useEffect,useContext} from 'react';
import axios from 'axios';
import { AuthContext } from '../../provider/AuthProvider'; //+
import myinfo_default from '../../../img/myinfo_default.jpg'; //  
import { useNavigate } from 'react-router-dom';

const Userinfo = () => {
    //     
    const openImageInNewTab = () => {
      const newWindow = window.open('', '_blank', 'width=600,height=400');
      newWindow.document.body.innerHTML = `
        <div style="display: flex; justify-content: center; align-items: center; height: 100vh;">
          <img src="${myinfo_default}" alt=" " style="min-width: 100%; min-height: 100%;" />
        </div>
      `;
    };

    const [user, setUser] = useState(null);//   (user)
    const { tokenCookie } = useContext(AuthContext);//   
    const navigate  = useNavigate();

    //   
    useEffect(() => {
      const fetchUser = async () => {
        try {
          if (!tokenCookie) { //  
            alert(" !");
            navigate('/login');
            return;
          }
          console.log(" : ", `Bearer ${tokenCookie}`);
          //     
          const response = await axios.get('/user/info', {
            headers: {
              // token: tokenCookie
              Authorization: `Bearer ${tokenCookie}` //  Authorization  
            }
          });
  
          setUser(response.data);

        } catch (error) {
          console.error("  ", error.message);
          if (error.response) {
            //       
            console.error("  :", error.response.status);
            console.error("  :", error.response.data);
          } else if (error.request) {
            //    
            console.error("  :", error.request);
          } else {
            console.error("      :", error.message);
          }
          alert(" !");
        }
      };
      fetchUser();
    }, []);
    
    if (!user) {
      return <div className='loading'> ...</div>;
    }
    
    return (
      <div className='content_body'>
        <div>
          <table className='userInfoTable'>
            <tbody>
              <tr>
                <th colSpan={2}>   </th>
              </tr>
              <tr>
                <td>
                  <ul>
                  <li> : {user.userId ? user.userId : ""}</li>
                  <li> : {user.userNickname ? user.userNickname : ""}</li>
                  <li> : {user.signUpDate ? user.signUpDate : ""}</li>
                  <li> : {user.userRole ? user.userRole : ""}</li>
                  </ul>
                </td>
                <td>
                  <div onClick={openImageInNewTab}>
                    <img src={myinfo_default} alt=" " className="my_profileImg" />
                  </div>
                </td>
              </tr>
              <tr>
                <td colSpan={2}>
                  <button> </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    );
  };
  
  export default Userinfo;