import React, { useState,useContext } from 'react';
import { AuthContext } from '../../provider/AuthProvider'; //+
import { NavLink,useNavigate,Outlet,Routes, Route } from 'react-router-dom';

const Myinfo = () => {

  const {userNicknameCookie} = useContext(AuthContext);

  const navigate = useNavigate();

  //    
  const [selectedItem, setSelectedItem] = useState(0);

  //   
  const handleItemClick = (index, path) => {
    setSelectedItem(index);
    navigate(path);
  };

  return (
    <div className='content_body'>
      <div className='myInfo_container'>
        <nav className='myinfo_header'>
          <div>
            <div className='profileImg'></div>
            <p><purple>{userNicknameCookie}</purple> !</p>
            <ul>
              <NavLink exact to="UserInfo"><li className={selectedItem === 0 ? 'myinfoChecked' : ''} onClick={() => handleItemClick(0, "/UserInfo")}>&nbsp;&nbsp;&nbsp;</li></NavLink>
              <NavLink  to={"MyProfile"}><li className={selectedItem === 1 ? 'myinfoChecked' : ''} onClick={() => handleItemClick(1, "/MyProfile")}>&nbsp;&nbsp;&nbsp; </li></NavLink >
              <NavLink  to={"MyQuestions"}><li className={selectedItem === 2 ? 'myinfoChecked' : ''} onClick={() => handleItemClick(2, "/MyQuestions")}>&nbsp;&nbsp;&nbsp; </li></NavLink >
              <NavLink  to={"MyPosts"}><li className={selectedItem === 3 ? 'myinfoChecked' : ''} onClick={() => handleItemClick(3, "/MyPosts")}>&nbsp;&nbsp;&nbsp; </li></NavLink >
              <NavLink  to={"Withdraw"}><li className={selectedItem === 4 ? 'myinfoChecked' : ''} onClick={() => handleItemClick(4, "/Withdraw")}>&nbsp;&nbsp;&nbsp;</li></NavLink >
            </ul>
          </div>
        </nav>
        <Outlet/>
      </div>
    </div>
  );
};

export default Myinfo;