import React, { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { AuthContext } from '../provider/AuthProvider'; //+

const Login = () => {
    const { login } = useContext(AuthContext); // AuthContext login  
    const navigate  = useNavigate();

    //  
    const [formValues, setFormValues] = useState({
        userId: '',
        userPwd: '',
    });
    
    //   
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormValues(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    const loginSubmit = async (e) => {
        e.preventDefault();

        if (!formValues.userId || !formValues.userPwd) {
            alert("   !");
            return;
        }

        try {
            const response = await axios.post('/user/login', formValues, {
                withCredentials: true
            });
            console.log(' :', response.data); //    

            if (response.data === "loginFailed") {
                alert(" .");
            } else if (response.status === 200) {
                const { token, userId, userNickname, userRole } = response.data;
    
                login(token, userId, userNickname,userRole); //    AuthProvider + 
    
                alert(" !");
                navigate('/');
                window.location.reload(); //  

            } else {
                alert(" !");
            }
        } catch (error) {
            if (error.response.status === 500) {
                alert(" .");
            } else {
                alert("!");
            }
            console.error(' :', error.response.data);
        }
    };

    return (
        <div className='content_body'>
            <div className='loginContainer'>
                <h3></h3>
                <form onSubmit={loginSubmit}>
                    <table className='loginTable'>
                    <tbody>
                        <tr>
                            <td><purple></purple></td>
                            <td>
                                <input type='text' name='userId' value={formValues.userId} onChange={handleChange} placeholder=' .' required />
                            </td>
                        </tr>
                        <tr>
                            <td><purple></purple></td>
                            <td>
                                <input type='password' name='userPwd' value={formValues.userPwd} onChange={handleChange} placeholder=' .' required />
                            </td>
                        </tr>
                        <tr>
                            <td colSpan={2}>
                                <button id='registerBtn' type='submit'></button>
                            </td>
                        </tr>
                    </tbody>
                    </table>
                </form>
            </div>
        </div>
    );
};

export default Login;